export default {
	data() {
		return {
			current:'',
			userinfo: [],
			personinfo: ["星辰岛", "北半球", "你给的爱太假", "5-s1-9720479", "我就是我,颜色不一样的烟火", "女"],
			settinginfo: ["12", '23', '45'],
			aboutus: [
				"我们都是来自五湖四海，为了一个共同的革命目标，走到一起来了。我们还要和全国大多数人民走这一条路。我们今天已经领导着有九千一百万人口的根据地⑷，但是还不够，还要更大些，才能取得全民族的解放。我们的同志在困难的时候，要看到成绩，要看到光明，要提高我们的勇气。中国人民正在受难，我们有责任解救他们，我们要努力奋斗。要奋斗就会有牺牲，死人的事是经常发生的。但是我们想到人民的利益，想到大多数人民的痛苦，我们为人民而死，就是死得其所。不过，我们应当尽量地减少那些不必要的牺牲。我们的干部要关心每一个战士，一切革命队伍的人都要互相关心，互相爱护，互相帮助。",
				"反恐精英：全球攻势（CS:GO）2020年6月4日更新补丁大小96MB，主要内容为改进了纹理流送功能和科洛林、阿努比斯两张地图。游戏客户端版本：1.37.5.5渲染纹理流送现在会加载标记为no mip maps或no lod的完整分辨率贴图（应该是修复之前某些创意工坊地图贴图错误）。"
			],
			portraitsrc: "https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=3023235837,3703646437&fm=26&gp=0.jpg",
		};
	},
	methods: {
		// 监听下拉刷新
		onPullDownRefresh(){
			this.userinfo = ""
			let jwt = uni.getStorageSync("skey");
			setTimeout(()=>{
				// this.getuserInfo()
				// if (jwt !== '') {
				// 	this.current = 0
				// 	this.getuserInfo()
				// } else {
				// 	this.current = 1
				// }
				//判断是否登录
				this.checkload()
				uni.stopPullDownRefresh()
			})
		},
		// 登录按钮点击事件
		wxLogin(e){
			const that = this;
			that.logining = true;
			// let userInfo = e.detail.userInfo;
			uni.login({
				provider: "weixin",
				success: (login_res => {
					let code = login_res.code;
					// console.log("code" + code);
					uni.request({
						url: 'http://47.240.8.112/api/v1/private/wx_login/',
						method: "POST",
						header: {
							'content-type': 'application/json'
						},
						data: {
							code: code,
							// user_info:userinfo
							// user_info: info_res.rawData
						},
						success(res) {
							console.log(res)
							// 保存jwt
							uni.setStorageSync("skey", res.data.jwt);
							// 保存id信息
							uni.setStorageSync("sid",res.data.user.id)
							// 刷新页面
							uni.startPullDownRefresh()
						},
						fail(error) {
							console.log(error)
						}
					})
				})
			})
			
		},
		openmysite() {
			uni.navigateTo({
				url: "../mysite/mysite"
			});
		},
		async getuserInfo() {
			const jwt = uni.getStorageSync("skey");
			const head = {
				'Authorization': "Bearer " + jwt
			};
			const user_id = uni.getStorageSync("sid");
			const result = await this.$myRequest({
				method: 'GET',
				url: '/users/' + user_id +'/',
				header: head,
			})
			console.log(result)
			this.userinfo = result.data

			// this.villagers = result.data.results;
			// console.log("村民"+this.villagers[1].id)
		},

		openaboutus() {
			uni.navigateTo({
				url: "./aboutus"
			});
		},
		openupdatelog() {
			uni.navigateTo({
				url: "./myupdate"
			});
		},
		onClickToMsg() {
			uni.navigateTo({
				url: '/pages/Message/Message'
			});
		},
		// 判断是否登录
		checkload() {
			let jwt = uni.getStorageSync("skey");
			if (jwt !== '') {
				this.current = 0
				this.getuserInfo()
			} else {
				this.current = 1
			}
		},
		onLoad() {
			// this.getuserInfo();
			this.checkload();
		},
		onShow() {
			this.checkload();
		}
	}
}
